import { Component, Input, OnInit } from '@angular/core';
import { MenuController } from '@ionic/angular';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {

  // @dev: Fernando: con el tema dark recomiendo esta imagen ../../../assets/logos/Otium_Transparet_No Shadow.png
  @Input() imgHeader: string = '../../../assets/logos/logo_XL.png'; // default image
  @Input() imgUser: string = '../../../assets/img/userDefault.png'; // default image

  constructor(private menuController: MenuController,
              private router: Router) { }

  ngOnInit() {
    let returnArrow = document.getElementById('returnarrow');
    console.log('this.router.url', this.router.url);

    if(this.router.url === '/welcome')
    returnArrow.remove();
  }

  openMenu() {
    this.menuController.open('header');
  }

}

