import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage-angular';
import { UserData } from '../interfaces/interfaces';

@Injectable({
  providedIn: 'root'
})
export class UsersService {

  private _storageUsers: Storage | null = null;
  private _userData: UserData[] = [];
  
  constructor(private storage: Storage) {
    this.init();
  }

  get getUser()  {
    
      console.log('geter en servicio', this._userData[0]);
      return  this._userData[0];
    
  }
  
  async init() {

    const storage = await this.storage.create();
    this._storageUsers = storage;
    this.loadUser();
  }

  async saveUser( userdata: UserData) {

    this._userData = [userdata];
    this._storageUsers.set('user', this._userData);

  } 

  async saveUserImage( img: string) {

    this._userData[0].img = img;
    this._storageUsers.set('user', this._userData);

  } 

  async loadUser() {

    try {
      const user = await this._storageUsers.get('user');
      console.log('funcion loadUser ',  user);
      this._userData = user; 
    } catch (error) {
      
    }
  }



}
